@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

body {
  margin: 0;
  font-family: 'Montserrat',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul{
  list-style: none;
  padding: 0;
}

body,
h1,
h2,
h3,
p,
ul,
li{
margin: 0;
}

img{
  max-width: 100%;
}

button{
  cursor: pointer;
}


